// -----------------
// Main page content
// -----------------
.main-page {
	width: 75%;
	margin-left: auto;
	padding: 0 40px; }

@media screen and (max-width: 739px) {
	.main-page {
		max-width: 100vw;
		margin-left: 0;
		width: 100%;
		padding: 0 10px; } }


section {
	padding: 0 0 3rem 30px; }

@media screen and (max-width: 739px) {
	section {
		padding: 0 0 2rem 0; } }



// ---------------------------------------------------
// Sticky sidebar menu
// Hides on mobile view
// ScrollSpy active styles (see JS tab for activation)
// ---------------------------------------------------
.sidebar-menu {
	position: sticky;
	top: 0;
	align-self: start;
	width: 140px;
	float: left;

	li.active {
		a {
			color: #333;
			font-weight: 800; } }

	a {
		text-decoration: none;
		display: block;
		padding: .125rem 0;
		color: #ccc;
		transition: all 50ms ease-in-out; } // ðŸ’¡ This small transition makes setting of the active state smooth

	a:hover, a:focus {}
	color: #666; }

// Hide the sidebar menu on mobile
@media screen and (max-width: 739px) {
	.sidebar-menu {
		display: none; } }

// The sidebar menu uses standard list markup
ul, ol {
	list-style: none; }

ol {
	padding: 0;
	overflow: scroll;
	white-space: nowrap; }

li {
	margin-left: 3rem;
	font-size: 0.9rem; }



// -------------------------
// Wrapper for video iframes
// -------------------------
.video-wrapper {
	width: 100%;
	height: 400px;
	margin: 10px 0; }



// -----------------------------------
// Sticky banner at bottom of viewport
// -----------------------------------
.banner {
	border-top: 4px solid orange;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	z-index: 7;
	text-align: center;
	background: #333;
	color: #fff;
	font-size: 0.9rem;
	font-style: italic;
	padding: 10px 20px 20px;

	a {
		color: #fff; } }

