img {
  max-width: 100%; }

img.logo {
	height: 65px;
	margin: 10px 0 0 39px; }

section img {
	width: auto;
	max-height: 65vh;
	border-radius: 4px; }

.caption { // Use captions on standalone section images
  color: #999;
  font-family: 'Vollkorn', serif;
  font-size: 0.94rem;
  margin-bottom: 30px;
  margin-left: 4px; }

@media screen and (max-width: 739px) {
  .caption {
    margin-top: -5px; } } // Align caption a bit tighter to image on mobile displays


// ----------
// Photo grid
// ----------
.photo-grid {
  display: grid;
  justify-items: stretch;
  grid-template-columns: repeat(auto-fill, minmax(140px, 230px) ); }

@media screen and (max-width: 739px) {
  .photo-grid {
    display: block; } }


// ------------------------------------------------------------------------
// Lightbox
// Adapted from Greg Bowler on Code Pen: https://codepen.io/g105b/pen/xuclB
// ------------------------------------------------------------------------
.thumbnail {
  padding: 0;
  display: inline-block;
  margin: 1px 3px;

  &:hover {
    opacity: 0.7; }

  img {
    display: block;
    height: auto; } }

@media screen and (max-width: 739px) {
  .thumbnail {
    margin: 0; // Reset for mobile display so that captions underneath thumbnail and regular section images have the same spacing
    img {
      max-width: 100%;
      border-radius: 0; } } }

.lightbox { // Each thumbnail needs a corresponding lightbox
  display: none;

  &:target {
    display: block; }

  a {
    position: fixed;
    display: flex;
    background: #000;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    z-index: 8;


    &::after {
      content: "\2715";
      position: fixed;
      z-index: 10;
      top: 0;
      right: 0;
      left: 0;
      background: rgba(0,0,0,0.5);
      text-align: right;
      font-size: 2.5em;
      font-weight: lighter;
      padding: 0 20px 0 0;
      color: #fff; }


    img {
      display: block;
      z-index: 9;
      width: auto;
      height: auto;
      max-height: 100vh;
      margin: auto;
      border-radius: 0; } } }

@media screen and (max-width: 739px) {
  .lightbox {
    a {
      img {
        height: auto;
        max-width: 100vw; } } } }

.lightbox-caption {
  font-family: 'Vollkorn', serif;
  position: fixed;
  z-index: 11;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100vw;
  height: auto;
  text-align: center;
  background: rgba(0,0,0,0.7);
  color: #fff;
  padding: 20px; }

@media screen and (max-width: 739px) {
  .lightbox-caption {
    padding: 10px 20px; } }


@-webkit-keyframes Show {
  from {
    opacity: 0;
    zoom: 2; } }




